
        const data = [{"#":"","title":"Многопользовательская сборка ракетного двигателя","---":"","description":"``` Приложение дает пользователям возможность осуществлять процесс сборки предоставленной модели в формате виртуальной реальности. Люди могут собирать ракетный двигатель как самостоятельно, так и в многопользовательской комнате, объединившись с напарниками. Процесс сопровождают различные подсказки и тексты с описаниями деталей. Все это, вкупе с глубоким погружением, способствует быстрому усвоению материала. При окончании сборки модель становится анимированной и демонстрирует свой принцип работы.","images":["/images/projects/Engine-VR/resized_141160470-a31d20dd-86bd-4be8-9308-a631227b5ba0.webp","/images/projects/Engine-VR/resized_141155619-bb28a0e8-fd3b-4359-97e5-36f3d1cf71c3.webp","/images/projects/Engine-VR/resized_141155697-f247130d-e9a9-4f7d-812c-7a328e3022af.webp","/images/projects/Engine-VR/resized_141155722-2e5c8eab-dfef-42f7-ae67-daf43e1756d6.webp"],"videos":["https://youtu.be/6FTMDRp5fN4"],"tags":["VR","Game"],"tech":["Unity","C#"],"developers":["Сиротенко М.Р.","Корчиков М.Д.","Смирнов М.А","Лысаков И.А.","Смирнов Я.А."],"site":"","sourcecode":[{"name":"Исходный код","link":"https://github.com/RTUITLab/Engine-VR"}],"link":"Engine-VR"},{"#":"","title":"ITLab (система управления лабораторией)","---":"","description":"Для организации работы людей в лаборатории создана система, учитывающая активность людей, участие в мероприятиях, оплату работы сотрудников. Система позволяет распределять нагрузку, обеспечивать прозрачность выполняемых действий и мероприятий. Сайт создан для внутреннего использования и доступ к имеют только сотрудники лаборатории. Проект состоит из нескольких бекендов, написанный на разных языках программирования, таких как C#, GO, Kotlin. Фронтенд реализован на фреймворке Vue.JS. Мобильное приложение для iOS реализовано на языке Swift с использованием фреймворка SwiftUI. Так же система имеет возможность уведомлять людей о изменениях, при помощи бота вконтакте или почты.","images":["/images/projects/ITLab/resized_1.webp","/images/projects/ITLab/resized_2.webp","/images/projects/ITLab/resized_ios_1.webp","/images/projects/ITLab/resized_ios_2.webp"],"videos":[],"tags":["Frontend","Backend","Mobile"],"tech":["Vue","ASP","Ktor","Swift"],"developers":["Макущенко М.А.","Романов Д.Е.","Комар Б.Г.","Кузнецов А.А.","Иванов М.Е."],"site":"","sourcecode":[{"name":"Фронтенд","link":"https://github.com/RTUITLab/ITLab-Front"},{"name":"Система событий/оборудования","link":"https://github.com/RTUITLab/ITLab-Back"},{"name":"Система уведомлений","link":"https://github.com/RTUITLab/ITLab-Notify"},{"name":"Система записей о работе","link":"https://github.com/RTUITLab/ITLab-Reports"},{"name":"Система учета зарплат","link":"https://github.com/RTUITLab/ITLab-Salary"},{"name":"Приложение для iOS","link":"https://github.com/RTUITLab/ITLab-iOS"}],"link":"ITLab"},{"#":"","title":"Клавиатура с отслеживанием рук","---":"","description":"``` Проект с механической клавиатурой в формате виртуальной реальности для шлемов Oculus Quest и Oculus Quest 2. Нажатия на клавиши осуществляются при помощи трекинга рук с камер шлема, никаких контроллеров! Текст, вводимый с клавиатуры, выводится на экран монитора. Клавиатура позволяет печатать на русском и английском языках.","images":["/images/projects/Oculus-HandTracking-Keyboard/resized_1.webp","/images/projects/Oculus-HandTracking-Keyboard/resized_2.webp","/images/projects/Oculus-HandTracking-Keyboard/resized_3.webp"],"videos":["https://youtu.be/V3ekuwMH8WU"],"tags":["VR","Game"],"tech":["Unity","C#"],"developers":["Смирнов Я.А."],"site":"","sourcecode":[{"name":"Исходный код","link":"https://github.com/RTUITLab/Oculus-HandTracking-Keyboard"}],"link":"Oculus-HandTracking-Keyboard"},{"#":"","title":"Шутер в виртуальной реальности","---":"","description":"Шутер в формате виртуальной реальности, позволяющий оттачивать стрельбу из оружия разных видов и уворачиваться от вражеских пуль. В игре можно бросить вызов сразу нескольким противникам, либо сначала потренироваться в спокойной обстановке. В таком режиме у пользователя есть возможность стрелять по бутылкам в баре и мишеням в тире. На выбор представлено 5 моделей оружия, 4 локации и 3 уровня сложности.","images":["/images/projects/VRDuel/resized_148579235-a4f9e25e-dc3c-49d8-b1a6-7dbabfa52a99.webp","/images/projects/VRDuel/resized_148580147-297f79e2-15e3-4b34-8108-9550982b0fe2.webp","/images/projects/VRDuel/resized_148580160-84ee7b40-83f9-439c-a37e-4e1184bd4dd5.webp","/images/projects/VRDuel/resized_148580059-71f366be-9f6c-44bf-a662-abf7e3e00003.webp"],"videos":[],"tags":["VR","Game"],"tech":["Unity","C#"],"developers":["Егоркин Н.Е.","Шорин И.А."],"site":"","sourcecode":[{"name":"Исходный код","link":"https://github.com/RTUITLab/VRDuel"}],"link":"VRDuel"},{"#":"","title":"BunnyHuntVR - Web VR шутер","---":"","description":"VR игра, обороняйте грядки от проворных и назойливых зайцев. Узнайте сколько морковки вы сможете сохранить.","images":["/images/projects/WebVRShoot/resized_BunnyHuntVR.webp","/images/projects/WebVRShoot/resized_BunnyHuntVR2.webp"],"videos":[],"tags":["VR","Game","Shooter"],"tech":["A-Frame","Javascript","Html"],"developers":["Соколов А.В","Иерусалимов И.Д."],"site":"https://webvrshoot.rtuitlab.dev/","sourcecode":[{"name":"Проект","link":"https://github.com/RTUITLab/WebVRShoot"}],"link":"WebVRShoot"}];
